/* faq(アコーディオン)
------------------------------------------------------ */
@use "../../../global/" as *;
@use "../../../base/setting" as *;



.accordion__item {
  border-bottom: 1px solid #ccc;
}


.accordion__title {
  position: relative;
  padding: 18px 0px 20px 0;
    font-weight: normal;
    cursor: pointer;
}


.accordion__title::after {
  position: absolute;
  content: "expand_more";
  top: 50%;
  right: 0;
  font-family: "Material Icons Round";
  font-size: rem(24);
  color: $c_key1;
  transform: translateY(-50%);
}

.accordion__title.active::after {
  content: "expand_less";
} 

.accordion__btn,.accordion__text {
  display: flex;
  align-items: center;
  font-size: rem(15);
}

.accordion__label {
  flex: 0 0 23px;
  margin-right: 15px;
  font-size: rem(26);
  font-weight: bold;
  color: $c_key1;
}

.accordion__body {
  display: none;
}

.accordion__text {
padding-bottom: 15px;
}