/* header
------------------------------------------------------ */
@use "../base/setting" as *;
@use "../global/mixin/breakpoint" as *;


.header__inner {
  @include z-index(header);
  width: 100%;
  display: flex;
  height: 60px;
  position: fixed;
  align-items: center;
  justify-content: space-between;
  padding-right: 20px;
  padding-left: 20px;
  transition: 0.3s;

  @include mq(lg) {
    height: 80px;
    padding-right: 35px;
    padding-left: 35px;
    margin: 0 auto;
  }
}

.header__inner--active {
  background-color: #fff;
  box-shadow: 0 3px 6px rgb(0 0 0 / 5%);
}

.header__logo {
  width: 96px;
  @include mq(lg) {
    width: auto;
  }
}

.header__btn {
  display: none;
}

.header__inner--active .header__btn {
  display: block;
}

