/* footer
------------------------------------------------------ */
@use "../base/setting" as *;
@use "../global/mixin/breakpoint" as *;

.footer {
  background-color: $c_black;
}

.footer__inner {
  padding: 50px 0;
  text-align: center;
  @include mq(lg) {
    padding: 100px 0;
  }
}

.footer__logo {
  margin: 0 auto;
  width: 87px;
  @include mq(lg) {
    width: auto;
  }
}
