/* グリッド
------------------------------------------------------ */
@use "../../../global/" as *;

.grid {
  display: grid;
  gap: 15px;
}

.grid--price {
  grid-template-columns: repeat(3,1fr);
  gap: 25px;
  @include mq(lg) {
    gap: 15px;
  }
}

/*PC3カラム*/
.grid--pc3col {
  @include mq(md) {
    grid-template-columns: repeat(3,1fr);
  }
}