/* cta(行動喚起)
------------------------------------------------------ */
@use "../../../base/setting" as *;
@use "../../../global" as *;

.cta__body {
  padding: 30px 0;
  color: $c_white;
  text-align: center;
  background-color: $c_key1;

  @include mq(lg) {
    padding: 40px 0 50px 0;
  }
}

.cta__title {
  font-size: rem(26);
  margin-bottom: 10px;
  font-weight: 400;
  @include mq(lg) {
  font-size: rem(64);
  margin-bottom: 15px;
  }
}
