/* セクション
------------------------------------------------------ */
@use "../../../global/" as *;
@use "../../../base/setting" as *;

.section__back-color {
  background-color: $c_key1;
}

.section__inner {
  margin: 0 auto;
  padding: 40px 30px;
  @include mq(lg) {
    max-width: $width;
  }
}

.section__inner--faq {
  @include mq(lg) {
  padding: 60px 30px 70px 30px;
  }
}

/* 波形のSVG画像の隙間防止
------------------------------------------------------ */
.section-border-top {
  transform: translateY(1px);
  vertical-align: bottom;
}

.section-border-bottom {
  transform: translateY(-1px);
}