@use "../../../global/" as *;
@use "../../../base/setting" as *;
.price-cards {
  overflow-x: auto;
  padding-top: 25px;
  position: relative;
  white-space: nowrap;
}

.price-card {
  display: inline-block;
    position: relative;
    width: 320px;
    text-align: center;
    padding: 32px 20px 0;
    border-radius: 8px;
    background-color: #fff;
    @include mq(lg) {
      width: auto;
      margin: 0;
    }
}

.price-card__header {
  padding-bottom: 20px;
}

.price-card__title {
  font-size: rem(26);
    font-weight: bold;
    color: $c_key1;
    margin-bottom: 13px;
}

.price-card__price {
  font-size: rem(48);
}

.price-card__price span {
  font-size: rem(40);
}

.price-card__item {
  padding: 25px 0;
  border-top: 1px solid #ccc;
}


.price-card__item-head {
  font-size: rem(18);
}

.price-card__item-text {
  font-weight: bold;
}

.price-card__label {
  position: absolute;
    top: -24px;
    left: 50%;
    width: 80%;
    font-size: 20px;
    font-weight: bold;
    line-height: 1;
    color: $c_key1;
    padding: 10px;
    transform: translateX(-50%);
    border: 2px solid $c_key1;
    border-radius: 50px;
    background-color: $c_white;
}

.price-card__scroll {
  position: absolute;
  top: 50%;
  right: 5%;
  font-size: 12px;
  color: #fff;
  padding: 10px;
  transform: translateY(-50%);
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.6);
  @include mq(lg) {
    display: none;
  }
}

.price-card__scroll-icon {
  display: block;
  font-size: 40px;
  text-align: center;
}