/* page-top
------------------------------------------------------ */
@use "../../../base/setting" as *;
@use "../../../global/" as *;
.page-top {
  @include z-index(page-top);
  position: fixed;
  right: 20px;
  bottom: 20px;
}

.page-top__link {
  display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    color: #fff;
    text-align: center;
    transition: opacity 0.3s;
    border-radius: 50%;
    background-color: #fff;
    box-shadow: 0 0 10px 0 rgb(0 0 0 / 20%);
    cursor: pointer;
    @include mq(lg) {
      width: 80px;
      height: 80px;
    }
}

.page-top__link::after {
  font-family: "Material Icons Round";
  content: '\e316';
    color: $c_key1;
    font-size: rem(24);
    @include mq(lg) {
      font-size: rem(48);
    }
}