/* タブカード（トレーナー）
------------------------------------------------------ */
@use "../../../base/setting" as *;
@use "../../../global/" as *;

.tab-card {
  display: none;
  overflow: hidden;
  border-radius: 10px;
  background-color: $c_white;
  animation-name: fadeUp;
  animation-duration: 0.8s;
  animation-fill-mode: forwards;
}
@keyframes fadeUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}


.tab-card.show {
  display: block;
}

.tab-card__body {
  padding: 20px;
}

.tab-card__text {
  font-size: rem(14);
  line-height: 1.6;
  @include mq(lg) {
    font-size: rem(15);
    line-height: 1.7;
  }
}